.card-body {
  padding: 3rem 0 !important;
}

.card-text {
  font-size: 0.9rem;
  padding: 0.4rem 1.9rem;
}

.container-fluid {
  padding-top: 6rem;
}

.overflow {
  overflow: hidden;
}

.card {
  transform: scale(1);
  transition: transform 0.5s ease;
}

.card:hover {
  transform: scale(1.03);
}
